.blog {
     &__contenedor {
          display: grid;
          gap: 2rem;
          @include tablet {
               grid-template-columns: repeat(2, 1fr);
          }
          @include desktop {
               grid-template-columns: repeat(3, 1fr);
          }
     }
     &__entrada {
          background-color: $blanco;
          border: 1px solid $gris;
     }
     &__texto {
          padding: 1rem 2rem;
          h3 { 
               margin: 0;
               color: $negro;
               text-align: left;
          }
     }
     &__fecha {
          font-weight: $bold;
          span {
               color: $celeste;
          }
     }

     /***** pagina interna de blog *****/
     &__tecnologias {
          h4 {
               margin: 0;
               font-family: $fuente_titulos;
               font-size: 2rem;
          }  
          ul {
               max-width: 30rem;
               display: flex;
               flex-direction: column;
               gap: 2rem;
               @include tablet {
                    flex-direction: row;
                    justify-content: space-between;
               }
          }
          button {
               background-color: $celeste;
               color: $blanco;
               font-family: $fuente_principal;
               padding: 1rem 3rem;
               display: flex;
               justify-content: space-between;
               align-items: center;
               border-radius: 0.4rem;
               width: max-content;
               cursor: pointer;
               border: none;
               span {
                    color: $blanco;
                    margin-left: 1rem;
               }
          } 
     }
     
}
