.mobile__menu {
     display: flex;
     justify-content: center;
     @include tablet {
          display: none;
     }
     img {
          width: 3rem;
     }
}

.mostrar {
     display: flex;
     flex-direction: column;
     visibility: visible;
     opacity: 1;
     @include tablet {
          flex-direction: row;
     }
}