.formulario {
     font-family: $fuente_textos;
     &__form {
          p {
               color: $grisOscuro;
          }
          legend {
               font-size: 2rem;
          }
          label {
               text-transform: uppercase;
               display: block;
               font-weight: $bold;
               span {
                    color: $rojo;
               }
          }
          input:not([type="submit"]),textarea,select {
               padding: 1rem;
               display: block;
               width: 100%;
               background-color: $blanco;
               border: 1px solid $gris;
               border-radius: 5px;
               margin-bottom: 1.7rem;
               outline: none;
               font-size: 1.4rem;
          }
          textarea {
               resize: none;
               height: 20rem;
          }
          input[type="radio"]{
               margin: 0;
               width: auto;
          }
         
     }
     &__div {
          display: flex;
          justify-content: space-between;
          align-items: center;
          max-width: 30rem;
     }
     &__btn {
          font-family: $fuente_titulos;
          margin-top: 2rem;
     }
}