.seccion {
    margin-top: calc($separacion / 2);
    margin-bottom: calc($separacion / 2);
}
.contenido-centrado {
    max-width: 80rem;
    margin: 0 auto;
}
.titulo {
    text-align: center;
    text-transform: uppercase;
    margin: 4rem 0;
}
.titulo::after {
    content: "";
    background-image: url(/build/img/separador.webp);
    margin: 0 auto;
    width: 100px;
    height: 30px;
    background-repeat: no-repeat;
    display: block;
}