.nosotros {
     &__contenedor {
          display: grid;
          gap: 2rem;
          @include tablet {
               grid-template-columns: repeat(2, 1fr);
          }
     }
     &__info {
          blockquote {
               font-family: $fuente_titulos;
               font-weight: $bold;
               margin: 0;
               display: flex;
               align-items: center;
               gap: 1rem;
               &::before {
                    content: "";
                    background-color: $naranja;
                    width: 10px;
                    height: 30px;
                    display: inline-block;
               }
          }
          p {
               text-align: justify;
          }
     }
}