html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #f8f8f8;
}

p {
  color: #000000;
  font-size: 1.6rem;
  font-family: "Montserrat", sans-serif;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-family: "Poppins", sans-serif;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Oswald", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

li {
  list-style: none;
}

ul {
  padding: 0;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
  margin: 0 auto;
}

.titulo {
  text-align: center;
  text-transform: uppercase;
  margin: 4rem 0;
}

.titulo::after {
  content: "";
  background-image: url(/build/img/separador.webp);
  margin: 0 auto;
  width: 100px;
  height: 30px;
  background-repeat: no-repeat;
  display: block;
}

.boton__naranja {
  background-color: #FF6C00;
  color: #FFFFFF;
  padding: 1rem 2rem;
  font-size: 1.6rem;
  font-weight: 700;
  transition: all 0.3s ease;
  display: block;
  text-align: center;
  cursor: pointer;
  border: none;
}
.boton__naranja:hover {
  background-color: #cc5600;
}

.boton__azul {
  background-color: #0D6EFD;
  color: #FFFFFF;
  padding: 1rem 2rem;
  font-size: 1.6rem;
  font-weight: 700;
  transition: all 0.3s ease;
  display: block;
  text-align: center;
  cursor: pointer;
  border: none;
}
.boton__azul:hover {
  background-color: #0257d5;
}

.boton__celeste {
  background-color: #17A2B8;
  color: #FFFFFF;
  padding: 1rem 2rem;
  font-size: 1.6rem;
  font-weight: 700;
  transition: all 0.3s ease;
  display: block;
  text-align: center;
  cursor: pointer;
  border: none;
}
.boton__celeste:hover {
  background-color: #117a8b;
}

.boton__rojo {
  background-color: #DC3545;
  color: #FFFFFF;
  padding: 1rem 2rem;
  font-size: 1.6rem;
  font-weight: 700;
  transition: all 0.3s ease;
  display: block;
  text-align: center;
  cursor: pointer;
  border: none;
}
.boton__rojo:hover {
  background-color: #bd2130;
}

.boton__verde {
  background-color: #71B100;
  color: #FFFFFF;
  padding: 1rem 2rem;
  font-size: 1.6rem;
  font-weight: 700;
  transition: all 0.3s ease;
  display: inline-block;
  text-align: center;
  cursor: pointer;
  border: none;
}
.boton__verde:hover {
  background-color: #507e00;
}

/*** layout ***/
.no-webp .inicio {
  background-image: url("../img/header.jpg");
}

.webp .inicio {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
  padding: 2rem 0;
}
.header__barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear;
  display: none;
}
@media (min-width: 768px) {
  .header__navegacion {
    display: flex;
    opacity: 1;
    visibility: visible;
  }
}
.header__lista {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.7rem;
}
@media (min-width: 768px) {
  .header__lista {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__item a {
  color: #FFFFFF;
}
.header__item a:hover {
  color: #FF6C00;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 80vh;
  }
}
@media (min-width: 768px) {
  .header.inicio .header__contenido {
    min-height: 80vh;
  }
}
.header__contenido {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header__titulo {
  color: #FFFFFF;
  text-align: left;
  font-weight: 700;
  max-width: 50rem;
}

.footer {
  background-color: #333333;
  padding: 1rem 0;
  margin-bottom: 0;
}
.footer__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__contenedor {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer__lista {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.7rem;
}
@media (min-width: 768px) {
  .footer__lista {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer__item a {
  color: #FFFFFF;
}
.footer__item a:hover {
  color: #FF6C00;
}
.footer__copy {
  color: #e1e1e1;
  text-align: center;
}

.footer {
  background-color: #333333;
  padding: 1rem 0;
  margin-bottom: 0;
}
.footer__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__contenedor {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer__lista {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.7rem;
}
@media (min-width: 768px) {
  .footer__lista {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer__item a {
  color: #FFFFFF;
}
.footer__item a:hover {
  color: #FF6C00;
}
.footer__copy {
  color: #e1e1e1;
  text-align: center;
}

.iconos__contenedor {
  display: grid;
  gap: 3rem;
}
@media (min-width: 480px) {
  .iconos__contenedor {
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (min-width: 768px) {
  .iconos__contenedor {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .iconos__contenedor {
    grid-template-columns: repeat(3, 1fr);
  }
}
.iconos__div {
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1.3rem;
  box-shadow: 0 2px rgba(0, 0, 0, 0.1);
}
.iconos__div img {
  width: 7rem;
  height: 7rem;
  margin: 0 auto;
}
.iconos__div h3 {
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
  margin: 1rem;
  font-size: 2.6rem;
  font-weight: 400;
}
.iconos__div p {
  text-align: justify;
}

.anuncios__contenedor {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .anuncios__contenedor {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .anuncios__contenedor {
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncios__div {
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
}
.anuncios__info {
  padding: 1.4rem 2.5rem;
}
.anuncios__info h3,
.anuncios__info p,
.anuncios__info h4 {
  margin: 1rem;
}
.anuncios__info p {
  text-align: justify;
}
.anuncios__info h4 {
  color: #FF6C00;
  font-family: "Poppins", sans-serif;
}
.anuncios__iconos {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.anuncios__iconos li {
  display: flex;
}
.anuncios__iconos img {
  margin-right: 1rem;
}
.anuncios__iconos p {
  font-weight: 700;
}
.anuncios__todos {
  margin-top: 3rem;
  display: flex;
  justify-content: flex-end;
}

.no-webp .contacto {
  background-image: url(/build/img/encuentra.jpg);
}

.webp .contacto {
  background-image: url(/build/img/encuentra.webp);
}

.contacto {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #FFFFFF;
  padding: 10rem 0;
}
.contacto__div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contacto h2 {
  margin: 0;
}
.contacto p {
  color: #FFFFFF;
}

.blog {
  /***** pagina interna de blog *****/
}
.blog__contenedor {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .blog__contenedor {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .blog__contenedor {
    grid-template-columns: repeat(3, 1fr);
  }
}
.blog__entrada {
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
}
.blog__texto {
  padding: 1rem 2rem;
}
.blog__texto h3 {
  margin: 0;
  color: #000000;
  text-align: left;
}
.blog__fecha {
  font-weight: 700;
}
.blog__fecha span {
  color: #17A2B8;
}
.blog__tecnologias h4 {
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-size: 2rem;
}
.blog__tecnologias ul {
  max-width: 30rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .blog__tecnologias ul {
    flex-direction: row;
    justify-content: space-between;
  }
}
.blog__tecnologias button {
  background-color: #17A2B8;
  color: #FFFFFF;
  font-family: "Oswald", sans-serif;
  padding: 1rem 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 0.4rem;
  width: max-content;
  cursor: pointer;
  border: none;
}
.blog__tecnologias button span {
  color: #FFFFFF;
  margin-left: 1rem;
}

.testimoniales__contenedor {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .testimoniales__contenedor {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .testimoniales__contenedor {
    grid-template-columns: repeat(3, 1fr);
  }
}
.testimoniales__div {
  background-color: #282f43;
  color: #FFFFFF;
  border-radius: 0.6rem;
  padding: 1rem;
  position: relative;
}
.testimoniales__texto p {
  text-align: justify;
  color: #e1e1e1;
}
.testimoniales__texto p::before {
  content: "";
  background-image: url(/build/img/comilla.svg);
  background-repeat: no-repeat;
  width: 3rem;
  height: 3rem;
  display: block;
  left: 2rem;
  position: absolute;
}
.testimoniales__texto p::after {
  content: "";
  background-image: url(/build/img/comilla.svg);
  background-repeat: no-repeat;
  width: 3rem;
  height: 3rem;
  display: inline-block;
  transform: rotate(180deg);
  position: absolute;
}
.testimoniales__texto img {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
  margin: 1rem 0;
  border: 4px solid #e1e1e1;
  margin: 0 auto;
  object-fit: cover;
}
.testimoniales__footer span {
  color: #FFC0CB;
}

.formulario {
  font-family: "Montserrat", sans-serif;
}
.formulario__form p {
  color: #333333;
}
.formulario__form legend {
  font-size: 2rem;
}
.formulario__form label {
  text-transform: uppercase;
  display: block;
  font-weight: 700;
}
.formulario__form label span {
  color: #DC3545;
}
.formulario__form input:not([type=submit]), .formulario__form textarea, .formulario__form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 5px;
  margin-bottom: 1.7rem;
  outline: none;
  font-size: 1.4rem;
}
.formulario__form textarea {
  resize: none;
  height: 20rem;
}
.formulario__form input[type=radio] {
  margin: 0;
  width: auto;
}
.formulario__div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 30rem;
}
.formulario__btn {
  font-family: "Poppins", sans-serif;
  margin-top: 2rem;
}

.mobile__menu {
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .mobile__menu {
    display: none;
  }
}
.mobile__menu img {
  width: 3rem;
}

.mostrar {
  display: flex;
  flex-direction: column;
  visibility: visible;
  opacity: 1;
}
@media (min-width: 768px) {
  .mostrar {
    flex-direction: row;
  }
}

.darkmode__contenido {
