.dark-mode .blog__texto p {
  color: #e1e1e1;
}
.dark-mode .nosotros__info blockquote, .dark-mode .nosotros__info p {
  color: #FFFFFF;
}
.dark-mode .formulario .formulario__form label, .dark-mode .formulario .formulario__form legend {
  color: #e1e1e1;
}
.dark-mode .formulario .formulario__form p {
