  .nosotros__contenedor {
    grid-template-columns: repeat(2, 1fr);
  }
}
.nosotros__info blockquote {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 1rem;
}
