.testimoniales {
     &__contenedor {
          display: grid;
          gap: 2rem;
          @include tablet {
               grid-template-columns: repeat(2, 1fr);
          }
          @include desktop {
               grid-template-columns: repeat(3, 1fr);
          }
     }
     &__div {
          background-color: $negroOscuro;
          color: $blanco;
          border-radius: 0.6rem;
          padding: 1rem;
          position: relative;
     }
     &__texto {
          p {
               text-align: justify;
               color: $gris;
               &::before {
                    content: '';
                    background-image: url(/build/img/comilla.svg);
                    background-repeat: no-repeat;
                    width: 3rem;
                    height: 3rem;
                    display: block;
                    left: 2rem;
                    position: absolute;
               }
               &::after {
                    content: '';
                    background-image: url(/build/img/comilla.svg);
                    background-repeat: no-repeat;
                    width: 3rem;
                    height: 3rem;
                    display: inline-block;
                    transform: rotate(180deg);
                    position: absolute;
               }
          }
          img {
               width: 8rem;
               height: 8rem;
               border-radius: 50%;
               margin: 1rem 0;
               border: 4px solid $gris;
               margin: 0 auto;
               object-fit: cover;
          }
     }
     &__footer {
          span {
               color: $rosado;
          }
     }
}