/** Media Queries **/
@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}


/******** botones ***********/

@mixin boton($color, $color_texto,$display) {
    background-color: $color;
    color: $color_texto;
    padding: 1rem 2rem;
    font-size: 1.6rem;
    font-weight: $bold;
    transition: all .3s ease;
    display: $display;
    text-align: center;
    cursor: pointer;
    border: none;
    &:hover {
        background-color: darken($color, 10%);
    }
}