.anuncios {
     &__contenedor {
          display: grid;
          gap: 2rem;
          @include tablet {
               grid-template-columns: repeat(2, 1fr);
          }

          @include desktop {
               grid-template-columns: repeat(3, 1fr);
          }
     }

     &__div {
          background-color: $blanco;
          border: 1px solid $gris;
     }

     &__info {
          padding: 1.4rem 2.5rem;

          h3,
          p,
          h4 {
               margin: 1rem;
          }

          p {
               text-align: justify;
          }

          h4 {
               color: $naranja;
               font-family: $fuente_titulos;
          }
     }

     &__iconos {
          display: flex;
          align-items: center;
          justify-content: space-between;

          li {
               display: flex;
          }

          img {
               margin-right: 1rem;
          }

          p {
               font-weight: $bold;
          }

     }

     &__todos {
          margin-top: 3rem;
          display: flex;
          justify-content: flex-end;
     }
}