.footer {
     background-color: $grisOscuro;
     padding: 1rem 0;
     margin-bottom: 0;
     &__contenedor {
          display: flex;
          flex-direction: column;
          align-items: center;
          @include tablet {
               flex-direction: row;
               justify-content: space-between;
          }
     }
     &__lista {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 1.7rem;
          @include tablet {
               flex-direction: row;
               justify-content: space-between;
          }
     }
     &__item a {
          color: $blanco;
          &:hover {
               color: $naranja;
          }
     }
     &__copy {
          color:$gris;
          text-align: center;
     }
}