.no-webp .contacto {
  background-image: url(/build/img/encuentra.jpg);
}
.webp .contacto {
  background-image: url(/build/img/encuentra.webp);
}
.contacto {
     background-position: center;
     background-size: cover;
     background-repeat: no-repeat;
     color: $blanco;
     padding: 10rem 0;
     &__div {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
     }
     h2 {
          margin: 0;
     }
     p {
          color: $blanco;
     }
}