.darkmode {
     &__contenido {
          position: relative;
          button {
               position: absolute;
               right: 3rem;
               bottom: 3rem;
               z-index: 9999;
               background-color: $azul;
               width: 4rem;
               height: 4rem;
               border: none;
               border-radius: 50%;
               outline: none;
               cursor: pointer;
               position: fixed;
               color: $blanco;
               box-shadow: 0 0 2px 4px rgba(0,0,0,.2);
               i {
                    font-size: 2.3rem;
               }
          }
          
     }
}

.dark-mode {
     /**** body **/
     background-color: $oscuroBody;
     /*** titulo **/
     .titulo {
          color: $blanco;
     }
     /**** iconos ****/
     .iconos__div {
          background-color: $oscuroDivIconos;
          color: $gris;
          p {
               color: $gris;
          }
     }
     /***** anuncios ****/
     .anuncios__div {
          background-color: $oscuroDivAnuncio;
          h3 {
               color: $gris;
          }
          p {
               color: $blanco;
          }
          .anuncios__precio {
               color: $celeste;
          }
          .anuncios__iconos {
               li {
                    img {
                         filter: invert(1);
                    }
               }
          }
          .anuncios__btn {
               background-color: $celeste;
          }
     }
     .anuncio__resumen {
          .anuncio__precio {
               color: $celeste;
          }
          .anuncio__texto {
               color: $gris;
          }
     }
     .anuncio__iconos {
          li {
               img {
                    filter: invert(1);
               }
               p {
                    color: $blanco;
               }
          }
          
     }
     /***** contacto index **/
     .boton__naranjado {
          background-color: $naranja;
     }
     /*** blog **/
     .blog__entrada {
          background-color: $oscuroDivBlog;
          border: 1px solid $grisOscuro;
          h3 {
               color: $blanco;
          }
          p {
               color: $gris;
          }
     }
     .blog__texto {
          h3,h4{
               color: $blanco;
          }
          p {
               color: $gris;
          }
     }
     /**** nosotros ***/
     .nosotros__info {
          blockquote,p {
               color: $blanco;
          }
     }
     /*** FORMULARIO de contacto ***/
     .formulario {
          .formulario__form {
               label,legend {
                    color: $gris;
               }
               p {
                    color: $blanco;
               }
          }
          input:not([type="submit"]),textarea,select {
               background-color: $oscuroDivContacto;
               color: $gris;
          }
          .formulario__btn {
               background-color: $celeste;
          }
          
     }
     
}