.no-webp .inicio {
     background-image: url("../img/header.jpg");
}
.webp .inicio {
     background-image: url("../img/header.webp");
}

.header {
     background-color: $grisOscuro;
     padding: 2rem 0;
     &__barra {
          display: flex;
          flex-direction: column;
          @include tablet {
               flex-direction: row;
               justify-content: space-between;
          }
     }
     &__navegacion {
          visibility: hidden;
          opacity: 0;
          transition: visibility 0.3s ease-in-out, opacity 0.3s linear;
          display: none;
          @include tablet {
               display: flex;
               opacity: 1;
               visibility: visible;
          }
     }
     &__lista {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 1.7rem;
          @include tablet {
               flex-direction: row;
               justify-content: space-between;
          }
     }
     &__item a {
          color: $blanco;
          &:hover {
               color: $naranja;
          }
     }
     &.inicio {
          background-position: center center;
          background-size: cover;
          background-attachment: fixed;
          @include tablet {
               min-height: 80vh;
          }
          @include tablet {
               .header__contenido {
                    min-height: 80vh;
               }
          }
     }
     &__contenido {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
     }
     &__titulo {
          color: $blanco;
          text-align: left;
          font-weight: $bold;
          max-width: 50rem;
     }
}