.anuncio {
     &__precio {
          color: $verde;
          font-size: 3rem;
          font-weight: $bold;
     }
     &__resumen {
          p {
               text-align: justify;
          }
     }
     &__iconos {
          max-width: 30rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          li {
               display: flex;
               img {
                    margin-right: 1rem;
               }
               p {
                    font-weight: $bold;
               }
          }
     }
}